import React, {Fragment} from 'react';
import {connect} from "react-redux";
import TabsManager from './../TabsManager/TabsManager';
import ConfigAlert from './ConfigAlert/ConfigAlert';
import Shop from './Shop/shop';



const ConfigShop = (props) => {

    let tabs = [
        {name : 'Shop', link: '/config-shop'},
        {name : 'Purchased', link: '/purchased'},
    ];


    return (
        <Fragment>

            <TabsManager data={tabs} license={props.licenses} user={props.currentUser} licenseBalance={true}/>

            <div className="dashboard-content">

                <Shop />

            </div>

            <ConfigAlert />

        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    licenses: state.licenses.licenses,
    currentUser: state.user.currentUser,
});


export default connect(mapStateToProps)(ConfigShop);
