import React from 'react';
import {IconPack} from './../../../Images/IconPack';
import './style.scss';
import Slider from '@material-ui/core/Slider';
import {setBuyCLicenseModal, setBuyPLicenseModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import TextField from "@material-ui/core/TextField/TextField";
import Button from "@material-ui/core/Button";

const LicensesNew = (props) => {

    const [value1, setValue1] = React.useState(1000);
    const [card1Component, setCard1Component] = React.useState(false);
    const [value2, setValue2] = React.useState(5000);
    const [card2Component, setCard2Component] = React.useState(false);


    let personalLicenseData = {
        dayCost: (8 - value1/100*0.1277).toFixed(2),
        daysAtAll: (value1 / (8 - value1/100*0.1277).toFixed(2) ).toFixed(0),
        configCost: (8 - value1/100*0.0797).toFixed(2),
        configAtAll: (value1 / (10 - value1/100*0.0797).toFixed(2)).toFixed(0),
    }

    let corporateLicenseData = {
        dayCost: (8 - value2/5000*0.4312).toFixed(2),
        daysAtAll: (value2 / (8 - value2/5000*0.4312).toFixed(2) ).toFixed(0),
        configCost: (8 - value2/5000*0.2797).toFixed(2),
        configAtAll: (value2 / (10 - value2/5000*0.2797).toFixed(2)).toFixed(0),
    }


    const handleChange1 = (event, newValue) => {
        setValue1(newValue);
    };
    const handleChange11 = (event) => {
         setValue1(event.target.value);
    };
    const handleChange2 = (event, newValue) => {
        setValue2(newValue);
    };
    const handleChange22 = (event) => {
        setValue2(event.target.value);
    };

    let licenseCardComponent = ( <div className="value1"><span className="doll">$</span>{value1}<div className="change" onClick={()=>{setCard1Component(true)}}>{IconPack.buttons.change}</div></div>);
    if(card1Component) {
        licenseCardComponent = ( <div className="value2">
            <TextField
                label="Amount to replenish"
                fullWidth
                rows={1}
                value={value1}
                onChange={(e)=>{handleChange11(e)}}
                variant="filled"
                className="inputDollar"
            />
            <div className="change" onClick={()=>{setCard1Component(false)}}>{IconPack.buttons.change}</div>
        </div>)
    }


    let licenseCardComponent2 = ( <div className="value1"><span className="doll">$</span>{value2}<div className="change" onClick={()=>{setCard2Component(true)}}>{IconPack.buttons.change}</div></div>);
    if(card2Component) {
        licenseCardComponent2 = ( <div className="value2">
            <TextField
                label="Amount to replenish"
                fullWidth
                rows={1}
                value={value2}
                onChange={(e)=>{handleChange22(e)}}
                variant="filled"
                className="inputDollar"
            />
            <div className="change" onClick={()=>{setCard2Component(false)}}>{IconPack.buttons.change}</div>
        </div>)
    }


    return (
        <div className="LicensesNew--dashboard">
            <div className="content">
                <header>
                    <h1 className="center">Choose a license</h1>
                    <h3 className="center">Indicate the amount of replenishment of the license you need</h3>
                    <p className="center">The cost of a license depends on the amount of the first and subsequent replenishment. The larger the replenishment amount, the lower the tariffs for services under the license. Use the slider to view rates.</p>
                </header>

                <main>
                    <div className="row">
                        <div className="license_card license_card--width ">
                            <div className="header">
                                <div className="line blue"></div>
                                <h4 className="center">Personal</h4>
                                <h6 className="center">For individual use</h6>

                                <div className="option">

                                    {licenseCardComponent}

                                    <div className="range">
                                        <Slider
                                            step={100}
                                            min={100}
                                            max={5000}
                                            value={value1} onChange={handleChange1}  />
                                    </div>
                                </div>

                                <div className="secondOption">
                                    <div>

                                        <div><span className="white">${personalLicenseData.dayCost}</span>/ day</div>
                                        <div><span className="white">${personalLicenseData.configCost}</span>/ config</div>

                                    </div>
                                    <div className="center">{personalLicenseData.daysAtAll} days or {personalLicenseData.configAtAll} configs</div>
                                </div>
                            </div>
                            <div className="footer">
                                <Button variant="contained" className="btn btn--blue" onClick={()=>{props.setBuyPLicenseModal({bool: true, data: value1 })}}>
                                    BUY LICENSE
                                </Button>
                            </div>
                        </div>




                        <div className="vs">
                            vs
                        </div>
                        <div className="license_card license_card--width ">
                            <div className="header">
                                <div className="line pink"></div>
                                <h4 className="center">Corporate</h4>
                                <h6 className="center">For teams use</h6>
                                <div className="option">
                                    {licenseCardComponent2}
                                    <div className="range">
                                        <Slider
                                            step={100}
                                            min={5000}
                                            max={50000}
                                            value={value2} onChange={handleChange2} aria-labelledby="continuous-slider" />
                                    </div>
                                </div>

                                <div className="secondOption">
                                    <div>

                                        <div><span className="white">${corporateLicenseData.dayCost} </span> / day</div>
                                        <div><span className="white">${corporateLicenseData.configCost} </span> / config</div>

                                    </div>
                                    <div className="center">{corporateLicenseData.daysAtAll} days or {corporateLicenseData.configAtAll} configs</div>
                                </div>
                            </div>
                            <div className="footer">
                                <Button variant="contained" className="btn btn--blue" onClick={()=>{props.setBuyCLicenseModal({bool: true, data: value2 })}}>
                                    BUY LICENSE
                                </Button>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="license_card--width">
                            The price is for individual use of the license by one person.
                        </div>
                        <div>

                        </div>
                        <div className="license_card--width">
                            Prices are per license user. The number of users is not limited.
                        </div>
                    </div>

                    <div className="grey-line"></div>
                </main>

                <footer>

                    <nav>
                        <ul>
                            <li>About License</li>
                            <li>Terms of Use</li>
                            <li>Refund Policy</li>
                            <li>FAQ Section</li>
                        </ul>
                    </nav>
                </footer>

            </div>
        </div>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setBuyCLicenseModal: (obj) => dispatch(setBuyCLicenseModal(obj)),
        setBuyPLicenseModal: (obj) => dispatch(setBuyPLicenseModal(obj))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LicensesNew);