import React, {Fragment} from 'react';
import './style.scss';
import TabsManager from './../../TabsManager/TabsManager';
import MessageTable from './MessageTable/MessageTable';
import ChatComponent from './ChatComponents/Chat';
import {connect} from "react-redux";

const Tickets = (props) => {

    let currentComponent;

    let tabs = [
        { name : 'Tickets', link: '/tickets'},
        { name : 'New Ticket', link: '/new-ticket'},
    ];

    let ticketsComponent = (
        <Fragment>
            <TabsManager data={tabs} support={true}/>
            <div className="dashboard-content">
                <MessageTable />
            </div>
        </Fragment>
    );


    if(props.appState.chatState.isChatOpen){
        return (<Fragment><ChatComponent /></Fragment>);
    }else {
        return ticketsComponent;
    }

};

const mapStateToProps = (state) => ({
    tickets: state.tickets.tickets,
    appState: state.appOptions.appState
});

export default connect(mapStateToProps)(Tickets);